
@-webkit-keyframes spinAround {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
@keyframes spinAround {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
.vld-demo-app {
  position: relative;
}
.vld-demo-app .map {
  height: 500px;
}
.vld-demo-app .map-panel {
  padding: 0;
}
.vld-demo-app .map-panel .panel-heading {
  -webkit-box-shadow: 0 0.25em 0.5em rgba(54, 54, 54, 0.2);
          box-shadow: 0 0.25em 0.5em rgba(54, 54, 54, 0.2);
}
.vld-demo-app .map-panel .panel-content {
  background: white;
  -webkit-box-shadow: 0 0.25em 0.5em rgba(54, 54, 54, 0.2);
          box-shadow: 0 0.25em 0.5em rgba(54, 54, 54, 0.2);
}
@media screen and (min-width: 1200px) {
.vld-demo-app .map-panel {
    position: absolute;
    top: 0;
    right: 0;
    max-height: 500px;
    width: 20em;
}
}
.vld-demo-app .feature-popup {
  position: absolute;
  left: -50px;
  bottom: 12px;
  width: 20em;
  max-width: none;
}
.vld-demo-app .feature-popup:after,
.vld-demo-app .feature-popup:before {
  top: 100%;
  border: solid transparent;
  content: ' ';
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
}
.vld-demo-app .feature-popup:after {
  border-top-color: white;
  border-width: 10px;
  left: 48px;
  margin-left: -10px;
}
.vld-demo-app .feature-popup:before {
  border-top-color: #cccccc;
  border-width: 11px;
  left: 48px;
  margin-left: -11px;
}
.vld-demo-app .feature-popup .card-content {
  max-height: 20em;
  overflow: auto;
}
.vld-demo-app .feature-popup .content {
  word-break: break-all;
}




