
@-webkit-keyframes spinAround {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
@keyframes spinAround {
from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}
to {
    -webkit-transform: rotate(359deg);
            transform: rotate(359deg);
}
}
.vld-sidebar {
  padding: 0 1rem;
}
.vld-sidebar > * {
  margin: .5rem 0;
}
.vld-sidebar .logo {
  text-align: center;
}
.vld-sidebar .links {
  text-align: center;
}
.vld-sidebar .links a {
  color: #7a7a7a;
}
.vld-sidebar .links a:hover {
  color: #4a4a4a;
}




